	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		data() {
			return {
				checked: true,
				checked2: true,
			};
		},
		methods: {
			onChange({detail}) {
				this.checked = detail
			}
			,
			onChange2({detail}) {
				Dialog.confirm({
                    title: '提示',
                    message: '是否切换开关？'
                }).then((res) => {
                    this.checked2 = detail
                }).catch((e) => {//on cancel
                    console.log(e)
                });
			}
		}
	};
